import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  url = 'https://api.themoviedb.org/3/movie/top_rated?api_key=2672b6213500788e01cf4b7c6ddd8edd&language=en-US&page=1';
  urlFindById = 'https://api.themoviedb.org/3/movie/';
  urlFindById2 = '?api_key=2672b6213500788e01cf4b7c6ddd8edd&language=en-US';
  urlTVS = 'https://api.themoviedb.org/3/tv/popular?api_key=2672b6213500788e01cf4b7c6ddd8edd&language=en-US&page=1';
  constructor(private _http: HttpClient) { }

  getTopMovies() {
    return this._http.get(this.url);
  }
  getMovieById(movieId) {
    return this._http.get(this.urlFindById + movieId + this.urlFindById2);
  }
  getTopTVShows() {
    return this._http.get(this.urlTVS);
  }
}
