import { Component, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit, OnDestroy {
@Output() confirmMovieClicked = new EventEmitter();
@Output() confirmTVShowClicked = new EventEmitter();
  constructor() { }

  ngOnInit() {
  }

  movie() {
    this.confirmMovieClicked.emit();
  }
  tvShow() {
    this.confirmTVShowClicked.emit();
  }


  ngOnDestroy() {
    this.confirmMovieClicked.unsubscribe();
    this.confirmTVShowClicked.unsubscribe();
  }

}
